header {
  height: 8vh;
  display: flex;
  justify-content: center;
  padding: 0 2%;
  align-items: center;
  background: linear-gradient(
    90deg,
    rgb(0, 0, 0) 40%,
    rgba(0, 0, 0, 0.082) 100%
  );
  color: white;
}
